'use client';

import { useState, useEffect } from 'react';

export default function SimpleTestPage() {
  const [data, setData] = useState<any>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    console.log('üîÑ Starting simple fetch test...');
    
    fetch('http://localhost:8000/tasks')
      .then(response => {
        console.log('üì° Response status:', response.status);
        if (!response.ok) {
          throw new Error(`HTTP ${response.status}`);
        }
        return response.json();
      })
      .then(data => {
        console.log('‚úÖ Fetch successful:', data);
        setData(data);
        setLoading(false);
      })
      .catch(err => {
        console.error('‚ùå Fetch error:', err);
        setError(err.message);
        setLoading(false);
      });
  }, []);

  if (loading) {
    return <div className="p-4">‚è≥ Simple loading...</div>;
  }

  if (error) {
    return <div className="p-4 text-red-500">‚ùå Simple error: {error}</div>;
  }

  return (
    <div className="p-4">
      <h1 className="text-xl font-bold mb-4">‚úÖ Simple Test Results</h1>
      <p>Found {data?.length || 0} tasks</p>
      <pre className="bg-gray-100 p-2 mt-4 text-xs">
        {JSON.stringify(data?.slice(0, 2), null, 2)}
      </pre>
    </div>
  );
}